import React, { useRef, useState } from "react";
import { useForm } from "react-hook-form";
import { Modal } from "react-bootstrap";
import styled from "styled-components";
import cancel from "./../logo/cancel.svg";
import backArrow from "../img/backArrow.svg";
import axios  from "axios";
import SignUp from "./SignUp";
import "./OTP.css";
import { useHistory, Redirect, Route } from "react-router";
const Register = styled.p`
	font-family: Gilmer Bold;
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	text-align: center;
	margin-buttom: 10px;
	color: #374b58;
`;

const OTP = (props) => {
	const history = useHistory();
	const [ErrorMsg, SetErrorMsg] = useState(null);
	const [message, setMessage] = useState(null);
    
	const [showSignUp, handleSignUp] = useState(false)
	const openSignUp =()=>{handleSignUp(true)}	   
	const closeSignUp =()=>{handleSignUp(false)}	
	

const validateOTP = async (otp)=>{
		await axios.post(`https://jeetar-eqtxqyoupa-nw.a.run.app/api/v1/auth/verify`, 
			{   
				"temp_token": props.token,
				"verification_code": otp
			 })
         .then(response => {
			 console.log(response.data);
			 props.close()
			 openSignUp();

		})
		 .then(result => console.log(result))
		 .catch(error => {
			if (error.response) {
			  // The request was made and the server responded with a status code
			  // that falls out of the range of 2xx
			  console.log(error.response.data.message);
			  SetErrorMsg(error.response.data.message);
	
			}
			  });
	
	}

const resendOTP = async (otp)=>{
	    console.log('otp:',props.phoneNumber, 'toke:', props.token);
		await axios.post(`https://jeetar-eqtxqyoupa-nw.a.run.app/api/v1/auth/verification`, 
			{   
				"temp_token": props.token,
				"phone_number": props.phoneNumber 
			 })
			 .catch(error => {
				if (error.response) {
				  // The request was made and the server responded with a status code
				  // that falls out of the range of 2xx
				  console.log(error.response.data);
				  setMessage(error.response.data.message);
		
				}
				  });
	
	}
	
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm({ mode: "all" });

	const onSubmit = async (data) => {
		if (data) {
			console.log('otp:',data.otp, 'toke:', props.token);
			validateOTP(data.otp);

			//props.close();
			//history.push("/home/category/new-products/new-products");
		}
	};

	const handleClose = () => {
		props.close();
	};

	return (
		<>  
		    <SignUp status ={showSignUp} close = {closeSignUp} open={openSignUp} token={props.token} phoneNumber={props.phoneNumber} />
			<Modal show={props.status}>
				<Modal.Header className="border-0 pb-0" id="modal__header">
					<div className="d-flex justify-content-between">
						<div className="back__arror__wrapper">
							<img
								src={backArrow}
								alt="arror_back"
								className="d-block-inline arrow_back"
								onClick={handleClose}
							/>
						</div>
						<div className="mx-5"></div>
						<p className="one__time__password pt-1 ms-4">One Time Password</p>
					</div>
					<img src={cancel} className="close" alt="cancel" onClick={handleClose} />
				</Modal.Header>

				<Modal.Body>
					<div className="">
						<p className="text-center please__enter_code">
							Please enter the code sent to you at {props.phoneNumber}
						</p>
					</div>
					<form onSubmit={handleSubmit(onSubmit)}>
						<div className="row g-2">
							<div className="col-12 phone-column">
								<div className="form-floating">
									<input
										type="tel"
										name="otp"
										maxLength="4"
										{...register("otp", { required: true, maxLength: 4 , pattern: /^[+-]?\d*(?:[.,]\d*)?$/,})}
										id="otp"
										className={
											"form-control form-field " +
											(errors.otp || ErrorMsg ? "error" : " ")
										}
										placeholder="Phone Number"
									/>
									{errors.otp && errors.otp.type === "required" && (
										<span className="errorMessage text-right">
											OTP number is required
										</span>
									)}
									{errors.otp && errors.otp.type === "pattern" && (
										<span className="errorMessage text-right">
											Number is expected
										</span>
									)}
									{ ErrorMsg && (
										<span className="errorMessage text-right">
											{ErrorMsg}
										</span>
									)}
								</div>
							</div>
						</div>

						<div className="col-12 form-group">
							<button type="submit" className="submit form-control btn-primary">
								Approve
							</button>
						</div>
						<div className="col-12 form-group text-center">
							<p className="code">
								Havent received your code?{" "}
								<span className="resend_otp" onClick={resendOTP} style={{cursor:'pointer'}}>Resend</span>
							</p>
							<p>
								{ message && (
										<span className="errorMessage text-right">
											{message}
										</span>
									)}</p>
						</div>
					</form>
				</Modal.Body>
			</Modal>
		</>
	);
};

export default OTP;
