import React, { useRef, useState ,useEffect} from "react";
import { Link as RouterLink, useNavigate, useHistory, } from 'react-router-dom';
import { useForm } from "react-hook-form";
import axios from "axios";
import { Modal } from 'react-bootstrap';
import styled from "styled-components";
import cancel from "./../logo/cancel.svg";
import backArrow from "../img/backArrow.svg";
import {reactLocalStorage} from 'reactjs-localstorage';

import {useSelector, useDispatch} from 'react-redux'
import {bindActionCreators} from "redux"
import {actionCreators} from '../state/index'




const Register = styled.p`
	font-family: Gilmer Bold;
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	text-align: center;
	margin-buttom: 10px;
	color: #374b58;
`;

const PasswordModal = (props) => {
  const {user} = useSelector((state)=>state);
  const dispatch = useDispatch();
  const {logIn} = bindActionCreators(actionCreators, dispatch);

	
const {
		register,
		handleSubmit,
		formState: { errors },
	  } = useForm({mode: 'all'});
	  
const history = useHistory();	 

const [userPassword, setuserPassword] = useState(null);
const [ErrorMsg, SetErrorMsg] = useState(null);
	  
	  useEffect(() => {
		if(userPassword){
		  loginUser();
		}
	  }, [userPassword])



const loginUser = async () =>{
		 try{
		   await axios.post(`https://jeetar-eqtxqyoupa-nw.a.run.app/api/v1/auth/login`, { 
			"password": userPassword,
			"identifier": props.phoneNumber
			})
		   .then(res => {
					 if(res.data.status==="success"){
						const {status, data, message} = res.data;
						reactLocalStorage.setObject('userData',data);
						logIn(data);
			            history.push("/home/category/new-products/new-products");
					 }
	 
		   }).catch(error => {
		
			 if (error.response) {
			   //console.log(error.response);
			   SetErrorMsg("Incorrect password, Please try again");
			   
			 }
			   });
		 }
		 catch(err){
		   console.log(err);
		 }
 	}
	  

  const handleClose = () => {
	props.close();
	};
	  
	
	const onSubmit = async data => {
		if (data) {
			reactLocalStorage.clear();
			setuserPassword(null)
            setuserPassword(data.password);   
			//console.log(userPassword)
			
		  }
		  
	  }
	  
	  
	return (
		<> 
  
		 <Modal show={props.status} >
		 		<Modal.Header className="border-0 tw-py-2" id="modal__header">
				 		<img
								src={backArrow}
								alt="arror_back"
								className="d-block-inline arrow_back"
								onClick={handleClose}
						/>
						<p className="one__time__password tw-text-center">Login with Password</p>
						
					    <img src={cancel} className="login-close" alt="cancel" onClick={handleClose} />
					
				</Modal.Header>

				<Modal.Body>
				<form onSubmit={handleSubmit(onSubmit)}>
						<div className="row">
						    <div className="col-12 form-group">	
								<div className="form-floating">
									<input
										type="password"
										name="password"
										{...register('password', 
											{
											required: true, 
											minLength:7,
											pattern: '', 
												
											})}
											className={"form-control form-field " + ((errors.password || ErrorMsg ) ? "error" : " ")}  
										placeholder="Password"
									/>
								
									{errors.password && errors.password.type === "required" && <span className="errorMessage">Password is required</span>}
									{errors.password && errors.password.type === "minLength" && <span className="errorMessage">Password must be at least 7 long</span>}
									{errors.password && errors.password.type === "pattern" && <span className="errorMessage">Password must contain at least one special character.</span>}
									{ErrorMsg && <span className="errorMessage">{ErrorMsg}</span>}
									<label for="floatingInput">Password</label>

								</div>
							</div>
							<div className="col-12 form-group" style={{marginTop:'-10px'}}>
							<button
								type="submit"
								className="submit form-control btn-primary"
							>
								Login
							</button>
						</div>
						</div>
						
						
					</form>
				     </Modal.Body>		
			   </Modal> 		   
		</>
	)
  }
export default PasswordModal;
