import React, { useState, useEffect } from "react";
import { useHistory, useParams, generatePath } from "react-router-dom";

import AnimateHeight from "react-animate-height";

const CategoryMenu = ({ showSubMenus = true, activeFunc, close, value, active, data }) => {
	const history = useHistory();
	const { cat, subCat } = useParams();

	const { category, slug, subcategories } = data;
	const [isOpen, setIsOpen] = useState(() => {
		return cat === slug;
	});

	const shouldOpen = cat === slug && isOpen;

	const handleSubClick = (value) => {
		const url = generatePath("/home/category/:cat/:subCat", {
			cat: slug,
			subCat: value,
		});
		history.push(url);
	};

	const handleClick = () => {
		const url = generatePath("/home/category/:cat/:subCat", {
			cat: slug,
			subCat: subcategories[0].slug,
		});
		history.push(url);

		if (shouldOpen) {
			setIsOpen(false);
		} else {
			setIsOpen(true);
		}
	};

	return (
		<div className="">
			<div
				onClick={handleClick}
				className={`${
					cat === slug ? "tw-bg-blue-100" : ""
				} tw-flex tw-px-3 tw-items-center tw-cursor-pointer tw-py-1`}
			>
				<div className="tw-flex tw-items-center">
					<div style={{ width: "51px", height: "51px" }}>
						<img
							src={require("../../../../assets/images/new-products-icon.svg").default}
							className="tw-w-full"
							alt=""
						/>
					</div>
					<span className="tw-ml-2 tw-font-bold">{category}</span>
				</div>
				<img
					src={require("../../../../assets/icons/chevron-up-blue.svg").default}
					className={`${
						cat === slug ? "tw-rotate-180" : "tw-rotate-0"
					} tw-w-3 tw-ml-auto tw-cursor-pointer tw-transform tw-transition-transform`}
					alt=""
				/>
			</div>
			<AnimateHeight
				id="example-panel"
				duration={500}
				height={shouldOpen ? "auto" : 0} // see props documentation below
			>
				<ul
					className="tw-pl-0 tw-mb-0"
					// style={{ transitionProperty: "height" }}
					// className={`${
					// 	hideSub ? "jt-animate-height-initial" : ""
					// } tw-duration-300 tw-overflow-hidden jt-animate-height-end`}
				>
					{subcategories.map((each) => {
						return (
							<li
								onClick={() => {
									handleSubClick(each.slug);
								}}
								className={`${
									subCat === each.slug ? "tw-bg-gray-100" : ""
								} tw-py-2 tw-cursor-pointer tw-pl-20`}
							>
								{each.name}
							</li>
						);
					})}
				</ul>
			</AnimateHeight>
		</div>
	);
};

export default CategoryMenu;
