import React, { useState } from "react";
import { Link, useNavigate, useHistory, } from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux'
import {bindActionCreators} from "redux"
import {actionCreators} from '../../state/index'
import {reactLocalStorage} from 'reactjs-localstorage';
const profileLinks = [
	
	{
		label: "My Addresses",
		to: "/home/profile/address",
	},
	{
		label: "Favourite Products",
		to: "/home/profile/favourite",
	},
	{
		label: "Previous Orders",
		to: "/home/profile/previous-order",
	},
	{
		label: "My Payment Methods",
		to: "/home/profile/payment",
	},
	{
		label: "Change Password",
		to: "/home/profile/password",
	},
	{
		label: "Communication Preferences",
		to: "/home/profile/preference",
	},
];

const NavProfile = () => {
	const {login} = useSelector((state)=>state);
	const dispatch = useDispatch();
    const {logOut} = bindActionCreators(actionCreators, dispatch);

	//console.log('token', user.user);
	const {user_data, token, expires} = login.user;
	const {first_name, last_name, phone_number, email} = user_data;

	const history = useHistory();	 

    const handleLogout = ()=>{
		logOut({user: null})
		history.push("/");
		
	}
   

	const [isOpen, setIsOpen] = useState(false);

	const closeOptions = () => {
		setIsOpen(false);
	};
	const toggleOptions = () => {
		setIsOpen((prev) => {
			return !prev;
		});
	};

	const handleClick = (value) => {
		closeOptions();
	};

	return (
		<div className="tw-relative tw-inline-block">
			<div onClick={toggleOptions} className="tw-flex tw-cursor-pointer tw-items-center">
				<img
					src={require("../../assets/icons/profile-icon.svg").default}
					className={`tw-w-4 tw-ml-3`}
					alt=""
				/>
				<div className="tw-flex tw-ml-2 tw-items-center">
					<span className="tw-text-jt-18 .jt-glb tw-text-white">Profile</span>
					<img
						src={require("../../assets/icons/chevron-up.svg").default}
						className={`${
							isOpen ? "tw-rotate-0" : "tw-rotate-180"
						} tw-w-4 tw-ml-3 tw-transform`}
						alt=""
					/>
				</div>
			</div>

			{isOpen && (
				<div
					style={{ top: "200%", width: "270px", left: "-85px" }}
					className="tw-bg-white tw-absolute tw-shadow-elevation1 tw-z-10"
				>
					<div className="tw-bg-jt-lightblue tw-flex tw-items-center tw-py-4 tw-px-3">
						<span className="tw-w-10 tw-h-10 tw-bg-white tw-rounded-sm tw-flex tw-items-center tw-justify-center">
							<img
								src={require("../../assets/icons/profile-transparent.svg").default}
								className={`tw-w-4`}
								alt=""
							/>
						</span>
					    <Link to="/home/profile" style={{textDecoration:'none'}}>
							<div className="tw-ml-2">
								<p className="tw-text-jt-black jt-gln tw-mb-1">
									{first_name}
								</p>
								<p className="tw-text-jt-gray1 jt-gln tw-text-jt-12 tw-mb-0">
									{phone_number}
								</p>
							</div>
						</Link>
					</div>
					<ul className="tw-mt tw-p-4 tw-pb-0 tw-border-b tw-border-gray-50 ">
						{profileLinks.map((each, index) => {
							return (
								<Link
									className={"tw-text-jt-14 hover:tw-text-jt-black hover:tw-no-underline tw-cursor-pointer tw-block tw-no-underline tw-mb-2 tw-text-jt-black jt-gln tw-py-3 tw-px-2"}
									to={each.to}
								>
									{each.label}
								</Link>
							);
						})}
					</ul>
					<ul className="tw-mt-2 tw-p-4 tw-pt-0 tw-pb-2">
						<button className="tw-outline-none tw-text-jt-black jt-gln hover:tw-text-jt-black tw-bg-transparent" onClick={handleLogout}>
							Logout
						</button>
					</ul>
				</div>
			)}
		</div>
	);
};

export default NavProfile;
