import React, { useState, useEffect } from "react";
import LoggedInCart from "./Cart";
import CategoryMenu from "./CategoryMenu";
import CategorySection from "./CategorySection";
import { useHistory, useParams } from "react-router-dom";
import { useCart } from "react-use-cart";

import EmptyBasket from "./EmptyBasket";
import GridProduct from "./GridProduct";
import ProductCategorySection from "./ProductCategorySection";
import { dummyData } from "./DummyData";

const ProductSection = () => {
	const {
		isEmpty,
		// cartTotal,
		// totalUniqueItems,
		// items,
		// updateItemQuantity,
		// removeItem,
		// emptyCart
	} = useCart();

	const history = useHistory();
	const { cat } = useParams();
	const [warehouse] = useState(dummyData);
	const [firstSubCat, setFirstSubCat] = useState("");
	const [firstCat, setFirstCat] = useState("");
	const [product, setProduct] = useState([]);

	// const cagList = [1, 2, 3, 4, 5];

	useEffect(() => {
		const category = warehouse.find((each) => {
			return each.slug === cat;
		});

		setProduct(category.subcategories);
		setFirstSubCat(category.subcategories[0].name);
		setFirstCat(category.category);
	}, [cat]);

	return (
		<section className="tw-mt-16 tw-mb-20">
			<div className="global-container2">
				<div className="tw-flex">
					<div className="jt-category-length tw-sticky tw-top-0">
						<div style={{ top: "20px" }} className="tw-sticky">
							<p className="tw-text-jt-black tw-font-bold tw-text-jt-14 jt-gln tw-mb-2">
								Categories
							</p>
							<CategorySection data={dummyData} />
						</div>
					</div>
					<div className="tw-flex-1 tw-mx-3">
						<div className="tw-flex tw-mb-2 tw-items-center">
							<p className="tw-text-jt-black tw-font-bold tw-mb-0 tw-text-jt-14 jt-gln">
								{firstCat}
							</p>{" "}
							<img
								src={
									require("../../../../assets/icons/chevron-right-blue.svg")
										.default
								}
								className="tw-w-2 tw-mx-3"
								alt=""
							/>
							<p className="tw-text-jt-black tw-font-bold tw-mb-0 tw-text-jt-14 jt-gln">
								{firstSubCat}
							</p>{" "}
						</div>
						<div>
							{product.map((each, index) => {
								return index === 0 ? (
									<ProductCategorySection data={each} showParagraph={false} />
								) : (
									<ProductCategorySection data={each} />
								);
							})}
						</div>
					</div>
					<div className="jt-cart-length tw-sticky tw-top-0">
						<div style={{ top: "20px" }} className="tw-h-84 tw-sticky">
							<p className="tw-text-jt-black tw-font-bold tw-sticky tw-top-0 tw-text-jt-14 jt-gln tw-mb-2">
								Basket
							</p>

							{isEmpty && <EmptyBasket />}
							{!isEmpty && <LoggedInCart />}
						</div>
					</div>
				</div>
			</div>
		</section>
	);
};

export default ProductSection;
